import {
  Autocomplete,
  Button,
  Checkbox,
  Grid,
  Tab,
  Tabs,
  TextField,
} from "@mui/material";
import "./App.css";
import AscendoIcon from "./assets/AscendoIcon";
import CheckBoxOutlineBlankIcon from "@mui/icons-material/CheckBoxOutlineBlank";
import CheckBoxIcon from "@mui/icons-material/CheckBox";
import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getAllBU, getAllPG, getSubDatasource, getUserInfo, updateSelectedBU, updateSelectedDS, updateSelectedPG } from "./redux/slice/zendeskSlice";
const icon = <CheckBoxOutlineBlankIcon fontSize="small" />;
const checkedIcon = <CheckBoxIcon fontSize="small" />;

function App() {
  const [tabValue,setTabValue]=useState(0)
  const dispatch=useDispatch()
  const {
    bu_details,
    selected_bu,
    user_details,
    pg_details,
    selected_pg,
    ds_details,
    selected_ds,
    zendesk_error,
    summarize_details,
  } = useSelector((state) => state.zendeskReducer);
  const handleSelectedDS = (option) => {
    if (selected_ds.includes(option)) {
      dispatch(
        updateSelectedDS(
          selected_ds.filter((item) => item.uuid !== option.uuid)
        )
      );
    } else {
      dispatch(updateSelectedDS([...selected_ds, option]));
    }
  };
  const handleClear = () => {
    dispatch(updateSelectedDS([]));
  };

  useEffect(()=>{
    dispatch(getUserInfo())
  },[])

  useEffect(() => {
    if (user_details) {
      dispatch(getAllBU());
    }
  }, [user_details]);

  useEffect(() => {
    if (selected_bu) {
      dispatch(getAllPG(selected_bu.uuid));
      dispatch(getSubDatasource(selected_bu.uuid));
    }
  }, [selected_bu]);
  return (
    <div className="App">
      <Grid
        sx={{
          height: "54px",
          bgcolor: "#071942",
          display: "flex",
          alignItems: "center",
          p: "16px",
        }}
      >
        <AscendoIcon />
      </Grid>

      <Grid sx={{ p: "16px" }}>
        <Grid>
          <Grid sx={{ display: "flex", gap: "16px" }}>
            <TextField sx={{ flex: "1 1 0" }} size="small" />
            <Grid
              sx={{
                height: "40px",
                backgroundColor: "#F9BE02",
                px: "12px",
                py: "7px",
                borderRadius: "6px",
                cursor: "pointer",
                ":hover": { backgroundColor: "rgba(249, 190, 2, 0.88)" },
              }}
            >
              <img src="https://storage.googleapis.com/exteneral_logo/sap/Predict.svg"></img>
            </Grid>
          </Grid>

          <Grid sx={{ display: "flex", justifyContent: "space-between",pt:"16px" }}>
            <Autocomplete
              fullWidth
              id="checkboxes-tags-demo"
              disableClearable
              options={bu_details}
              value={selected_bu}
              size="small"
              onChange={(event, newValue) => {
                dispatch(updateSelectedBU(newValue));
              }}
            
              disableCloseOnSelect
              getOptionLabel={(option) => option.name}
              isOptionEqualToValue={(option, value) =>
                option?.uuid === value?.uuid
              }
              renderInput={(params) => (
                <TextField {...params} label="Business Unit" />
              )}
            />
            <Autocomplete
              fullWidth
              id="checkboxes-tags-demo"
              disableClearable
              options={pg_details}
              value={selected_pg}
              size="small"
              onChange={(event, newValue) => {
                dispatch(updateSelectedPG(newValue));
              }}
             
              disableCloseOnSelect
              getOptionLabel={(option) => option.name}
              isOptionEqualToValue={(option, value) =>
                option.uuid === value.uuid
              }
              style={{ marginLeft: "16px" }}
              renderInput={(params) => <TextField {...params} label="Product Group" />}
            />

            <Autocomplete
              fullWidth
              multiple
              id="checkboxes-tags-demo"
              options={ds_details}
              value={selected_ds}
              limitTags={1}
              size="small"
              disableCloseOnSelect
              getOptionLabel={(option) => option.name}
              isOptionEqualToValue={(option, value) =>
                option.uuid === value.uuid
              }
              slotProps={{ clearIndicator: { onClick: handleClear } }}
              ChipProps={{
                style: {
                  backgroundColor: "rgba(26, 115, 232, 0.08)",
                  color: "#1A73E8",
                  maxWidth: "100px",

                  width: "65px",
                  minWidth: "50px",
                  fontSize: "12px",
                  height: "18px",
                  borderRadius: "4px",
                  marginBottom: "6px",
                  flexDirection: "row",
                },
              }}
           
              renderOption={(props, option, { selected }) => {
                const isSelected = selected_ds?.includes(option);
                return (
                  <li
                    style={{ marginLeft: "-12px" }}
                    {...props}
                    onClick={() => handleSelectedDS(option)}
                  >
                    <Checkbox
                      icon={icon}
                      checkedIcon={checkedIcon}
                      checked={isSelected}
                    />
                    {option.name}
                  </li>
                );
              }}
              style={{ marginLeft: "16px" }}
              renderInput={(params) => (
                <TextField {...params} label="Datasources" />
              )}
            />
          </Grid>
          <Grid sx={{pt:"16px",borderBottom:"0.5px solid grey"}}>
            <Tabs value={tabValue} onChange={(e,newValue)=>setTabValue(newValue)} >
              <Tab value={0} label="SUMMARIZE"  />
              <Tab value={1} label="SOLUTION"/>
              <Tab value={2} label="GENERATE AI"/>
            </Tabs>
          </Grid>
        </Grid>
      </Grid>
    </div>
  );
}

export default App;
